* {
  font-family: "Source Sans Pro", sans-serif;
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: inherit;
}

ul[class],
ol[class],
li[class] {
  list-style-type: none;
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  font-weight: inherit;
  font-size: inherit;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  background-color: inherit;
  font-size: inherit;
  font-weight: inherit;
  color: inherit;
}

a {
  text-decoration: none;
}

html,
body {
  height: 100%;
}

body {
  background-color: rgb(248, 246, 242);
}

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.content {
  flex: 1 0 auto;
}

.main {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

.container {
  max-width: 1120px;
  margin: 0 auto;
  padding: 0 15px;
  height: 100%;
}

.header {
  background-color: #fff;
  position: fixed;
  width: 100%;
  border-bottom: 1px solid rgba(184, 171, 148, 0.4);
}
.header__logo-item {
  display: flex;
  width: auto;
}
.header__logo {
  color: rgba(44, 44, 44, 0.714);
  font-size: 25px;
  font-weight: 700;
}
.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  height: 53px;
  padding: 0 10px 0 10px;
}
.header__button-exit {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 100px;
  border-left: 1px solid 1px solid rgba(184, 171, 148, 0.4);
}
.header__exit {
  color: rgba(44, 44, 44, 0.714);
  font-size: 19px;
  letter-spacing: 0.01em;
  cursor: pointer;
}
.header__exit:hover {
  font-weight: 600;
}

.menu {
  display: flex;
  width: 100%;
  justify-content: center;
  padding-left: 350px;
}
.menu__list {
  display: flex;
}
.menu__list-item {
  display: flex;
}
.menu__list-item + .menu__list-item {
  padding-left: 280px;
  padding-right: 15px;
}
.menu__list-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 120px;
  height: 35px;
  color: rgba(44, 44, 44, 0.714);
  border: unset;
  border-radius: 10px;
  font-size: 19px;
  letter-spacing: 0.01em;
  cursor: pointer;
}
.menu__list-link:hover {
  background-color: rgb(240, 235, 226);
}

.aside__items {
  background-color: #fff;
  border: 1px solid rgba(184, 171, 148, 0.4);
  border-radius: 10px;
  width: 250px;
  height: auto;
  padding: 20px;
  display: flex;
  align-items: center;
  flex-direction: column;
  position: fixed;
}
.aside__signature {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding-bottom: 15px;
  position: relative;
}
.aside__signature::after {
  content: "";
  position: absolute;
  bottom: 0;
  width: 250px;
  height: 1px;
  background-color: rgba(184, 171, 148, 0.4);
}
.aside__item-image {
  object-fit: cover;
  border-radius: 50%;
  width: 100px;
  height: 100px;
}
.aside__item-name {
  color: rgba(44, 44, 44, 0.714);
  font-size: 20px;
  font-weight: 600;
  max-width: 200px;
  padding-bottom: 5px;
}
.aside__item-text {
  max-width: 200px;
  color: rgba(44, 44, 44, 0.714);
  font-size: 14px;
  text-align: center;
}
.aside__subscribe-item {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
.aside__subscribe-button {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: 40px;
  width: 250px;
  margin-top: 10px;
  margin-bottom: -15px;
  color: rgba(44, 44, 44, 0.714);
  font-size: 17px;
  font-weight: 600;
  padding-left: 30px;
  background-color: rgb(240, 235, 226);
  border: 1px solid rgba(184, 171, 148, 0.4);
  cursor: pointer;
}
.aside__subscribe-button:hover {
  background-color: rgba(184, 171, 148, 0.4);
}
.aside__menu-list {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  margin-top: 15px;
}
.aside__menu-link {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: 40px;
  width: 250px;
  color: rgba(44, 44, 44, 0.714);
  font-size: 17px;
  font-weight: 600;
  padding-left: 30px;
  cursor: pointer;
}
.aside__menu-link:hover {
  background-color: rgb(240, 235, 226);
}

.center__inner {
  display: flex;
  flex-direction: column;
}

.blog {
  background-color: rgb(248, 246, 242);
  padding-top: 77px;
  height: 100%;
}
.blog__inner {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.blog__share {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #fff;
  border: 1px solid rgba(184, 171, 148, 0.4);
  border-radius: 10px;
  width: 100%;
  height: 120px;
  padding: 20px;
}
.blog__share-name {
  color: rgba(44, 44, 44, 0.714);
  border: unset;
  width: 200px;
  height: 30px;
  padding-left: 25px;
}
.blog__share-item {
  display: flex;
  align-items: center;
  color: rgba(44, 44, 44, 0.714);
  border-radius: 10px;
  font-size: 18px;
  font-weight: 600;
}
.blog__share-post {
  display: flex;
}
.blog__share-form {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 40px;
  border: 1px solid rgba(184, 171, 148, 0.4);
  width: 100%;
  border-radius: 10px;
  padding-left: 25px;
  font-size: 18px;
}
.blog__share-form:hover {
  background-color: rgb(248, 246, 242);
}
.blog__input-post {
  display: flex;
  justify-content: space-between;
  background-color: inherit;
  width: 80%;
}
.blog__input-post:focus {
  outline: none;
}
.blog__share-btn {
  display: flex;
  width: 20%;
  height: 100%;
  align-items: center;
  justify-content: center;
  color: rgba(44, 44, 44, 0.714);
  background-color: rgb(248, 246, 242);
  border: 1px solid rgba(184, 171, 148, 0.4);
  border-radius: 10px;
  font-size: 18px;
  letter-spacing: 0.01em;
  cursor: pointer;
}
.blog__share-btn:hover {
  background-color: rgb(240, 235, 226);
}

.border {
  display: flex;
  justify-content: center;
  margin: 20px 0 20px 0;
}
.border__item {
  width: 99%;
  height: 1px;
  background-color: rgba(184, 171, 148, 0.4);
}

.article {
  width: 810px;
}
.article__post {
  width: 100%;
  padding: 20px;
  min-height: 50px;
  background-color: #fff;
  border: 1px solid rgba(184, 171, 148, 0.4);
  border-radius: 10px;
  margin-bottom: 25px;
}
.article__post-text {
  color: rgba(44, 44, 44, 0.714);
}

.signin {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.form {
  display: flex;
  justify-content: center;
}
.form__inner {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #fff;
  width: 100%;
  height: 100%;
  padding: 25px;
  border: 1px solid rgba(184, 171, 148, 0.4);
  border-radius: 10px;
}
.form__top {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-bottom: 20px;
  padding-left: 10px;
}
.form__title {
  color: rgba(44, 44, 44, 0.714);
  font-size: 23px;
  font-weight: 600;
  padding-bottom: 5px;
}
.form__subtitle-item {
  display: flex;
  align-items: center;
  color: rgba(44, 44, 44, 0.714);
}
.form__place {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 20px;
}
.form__place-input {
  width: 100%;
  height: 40px;
  border: 1px solid rgba(184, 171, 148, 0.4);
  border-radius: 10px;
  padding-left: 25px;
  font-size: 18px;
}
.form__place-input:hover {
  background-color: rgb(248, 246, 242);
}
.form__place-input:focus {
  outline: none;
}
.form__place-input + .form__place-input {
  margin-top: 10px;
}
.form__item-btn {
  display: flex;
  margin-bottom: 20px;
}
.form__item-submit {
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(44, 44, 44, 0.714);
  font-size: 17px;
  width: 100%;
  height: 40px;
  font-weight: 600;
  border: 1px solid rgba(184, 171, 148, 0.4);
  border-radius: 10px;
  background-color: rgb(240, 235, 226);
  cursor: pointer;
}
.form__item-submit:hover {
  background-color: rgb(231, 224, 211);
}
.form__down {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
.form__down-item {
  display: flex;
}
.form__down-question {
  display: flex;
  align-items: center;
  color: rgba(44, 44, 44, 0.714);
  padding-right: 12px;
}
.form__down-btn {
  display: flex;
}
.form__down-redirect {
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(44, 44, 44, 0.714);
  font-size: 17px;
  width: 130px;
  height: 40px;
  font-weight: 600;
  border: 1px solid rgba(184, 171, 148, 0.4);
  border-radius: 10px;
  background-color: rgb(240, 235, 226);
  cursor: pointer;
}
.form__down-redirect:hover {
  background-color: rgb(231, 224, 211);
}

.signup {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.msg {
  padding-bottom: 10px;
}
.msg__item {
  color: rgb(189, 32, 32);
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(248, 246, 242);
}
.footer__logo {
  color: rgba(44, 44, 44, 0.714);
  font-size: 20px;
  font-weight: 700;
  display: flex;
  align-items: center;
  height: 40px;
}